@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.infoBox {
  flex: 1 1;
  cursor: pointer;
}

.infoBox:not(:last-child) {
  margin-right: 20px;
}

.infobox__cases {
  color: #cc1034;
  font-weight: 600;
  font-size: 1.75rem;
  margin-bottom: 0.5rem;
}

.infobox__total {
  color: #6c757d;
  font-weight: 700 !important;
  font-size: 0.8rem !important;
  margin-top: 15px !important;
}

.infoBox--selected {
  border-top: 10px solid greenyellow;
}

.infoBox--red {
  border-color: red;
}

.infoBox__cases--green {
  color: lightgreen !important;
}

.map {
  height: 500px;
  background-color: white;
  padding: 1rem;
  border-radius: 20px;
  margin-top: 16px;
  box-shadow: 0 0 8px -4px rgba(0, 0, 0, 0.5);
}

.map .leaflet-container {
  height: 100%;
}

.info-container {
  width: 150px;
}

.info-flag {
  width: 100%;
  height: 80px;
  background-size: cover;
  border-radius: 8px;
}

.info-name {
  text-align: center;
  font-size: 20px;
  font-weight: bold;
  color: #555;
}

.info-confirmed,
.info-recovered,
.info-deaths {
  font-size: 14px;
  margin-top: 5px;
}

.table {
  margin-top: 20px;
  overflow: scroll;
  height: 400px;
  color: #6a5d5d;
}

.table tr {
  display: flex;
  justify-content: space-between;
}

.table td {
  padding: 0.5rem;
}

/* select every table row/record that is odd */
.table tr:nth-of-type(odd) {
  background-color: #f3f2f8;
}

/* Selectors' name follow BEM naming convention */

* {
  margin: 0;
}

body {
  background-color: #f5f6fa;
  font-family: "Open Sans", sans-serif;
  width: 100%;
}

.app {
  display: flex;
  justify-content: space-evenly;
  width: 70%;
  padding: 20px;
  margin: 0 auto;
}

.app__dropdown {
  background-color: white;
}

.app__left {
  flex: 0.95 1;
}

.app__header > h1 {
  color: #fc3c3c;
  font-size: 2rem;
}

.app__header {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  justify-content: space-between;
}

.app__stats {
  display: flex;
  justify-content: space-between;
}

/* flex takes up the entire container width and height */
.app__right {
  display: flex;
}

.app__right .MuiCardContent-root {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.app__graph {
  flex-grow: 1;
}

.app__graphTitle {
  margin-top: 15px;
  margin-bottom: 10px;
}

@media (max-width: 990px) {
  #root {
    position: static;
  }

  body {
    position: static;
  }

  .app {
    flex-direction: column;
    width: 100%;
  }
}

